.Container {
  display: flex;
  flex-direction: column;
  flex: 1;
  width: 100%;
  .Row-Title {
    font-size: 17px;
    font-weight: bold;
    font-family: Roboto;
    margin-right: auto;
    margin-bottom: 5px;
  }
  .Container-Row {
    display: flex;
    flex-direction: row;
    width: 100%;
    .Container-Row-Title {
      font-size: 14px;
      font-family: Roboto;
      text-align: start;
      width: 90%;
    }
    .Container-Row-Input {
      box-sizing: border-box;
      margin: 0px;
      font-variant: tabular-nums;
      list-style: none;
      position: relative;
      display: inline-block;
      flex: 1;
      min-width: 0;
      padding: 4px 11px;
      color: #595c97;
      font-size: 14px;
      line-height: 2;
      background-color: #ffffff;
      background-image: none;
      border: 1px solid #e4e9f0;
      border-radius: 2px;
      transition: all 0.3s;
      &:hover,
      &:focus,
      &:active {
        outline: none;
        border: 1px solid;
        border-color: #595c97;
      }
    }
    .Container-Row-Input-Error {
      border-color: #f5222e !important;
    }
  }
  .Error {
    opacity: 0;
    display: flex;
    min-height: 24px;
    .Error-Text {
      padding-top: 4px;
      font-size: 12px;
      font-family: Roboto;
      color: #f5222e;
    }
  }
  .Error-Trans {
    opacity: 1;
    transition: opacity ease-in 0.3s;
  }
}
